body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

.App {
  padding-left: 20px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.todo {
  -webkit-transition: width .075s, background-color .075s;
  transition: width .075s, background-color .075s;
  width: 350px;
  padding: 15px;
  margin-bottom: 5px;
  cursor: pointer;
}

.todo:hover {
  width: 370px;
  padding-right: 35px;
}

.todo__status {
  font-weight: bold;
}

.todo_completed {
  background-color: rgba(144, 238, 144, 1);
}

.todo_completed:hover {
  background-color: rgba(144, 238, 144, .7);
}

.todo_uncompleted {
  background-color: rgba(255, 99, 71, 1);
}

.todo_uncompleted:hover {
  background-color: rgba(255, 99, 71, .7);
}
.add-todo {
  width: 350px;
  padding: 15px;
  margin-bottom: 5px;
  background-color: plum;
}

.add-todo__input {
  padding: 5px;
  margin: 0;
  border: none;
  width: 100%;
}
.filter {
  width: 350px;
  padding: 15px;
  background-color: wheat
}

.filter__title {
  font-weight: bold;
}
.filter {
  margin-bottom: 5px;
}

.filter-link {
  -webkit-transition: background-color .075s;
  transition: background-color .075s;
  display: block;
  cursor: pointer;
  padding: 5px;
  background-color: tan;
  color: black;
  margin-bottom: 5px;
  text-decoration: none;
}

.filter-link_active {
  font-weight: bold;
}

.filter-link:last-child {
  margin-bottom: 0px;
}

.filter-link:hover {
  background-color: #dcc097;
}
